import React from 'react'
import cx from 'classnames'
import PropTypes from 'prop-types'
import './style.scss'

export default class StripeDivider extends React.Component {
  static propTypes = {
    data: PropTypes.arrayOf(PropTypes.number).isRequired,
    colors: PropTypes.arrayOf(PropTypes.string)
  }

  static defaultProps = {
    data: [],
    colors: ['#8CD0F1', '#00AAFF', '#FBB62D'],
    count: 48,
    direction: 'horizon'
  }

  render () {
    const props = this.props

    let sum = props.data.reduce((cur, pre) => cur + pre, 0)

    let curTotal = 0
    let percentArr = props.data.map((v, i) => {
      curTotal += v
      return curTotal / sum
    })

    return (
      <div style={props.style} className={cx('dui-strip', props.direction, props.className)}>
        {
          Array.from({length: props.count}).map((v, index) => {
            let indexPercent = (index + 1) / props.count

            let colorIndex = percentArr.findIndex(v => indexPercent <= v)

            let style = {...props.itemStyle || {}, background: props.colors[colorIndex % props.colors.length]}
            return <span key={index} style={style} className={cx('dui-strip-item', props.itemClass)} />
          })
        }
      </div>
    )
  }
}
